/* Imports ================================= */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

body {
	line-height: 1;
}

ol,
ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
	font-size: 16px;
	height: 100%;
	background: black;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	color: white;
}

p {
	color: white;
}

.grid-container {
	display: grid;
	margin: 4rem 2rem 2rem 2rem;
	grid-auto-rows: auto;
	grid-auto-columns: auto;
	grid-auto-flow: dense;
	gap: 10px;
	grid-template: "img1 img1 img2 img2" 1fr "img6 img7 img7 img8" 1fr "img9 img10 img11 ." 1fr "img12 img12 img12 img12" 1fr "img13 img14 img14 img15" 1fr "img16 img16 img16 img16" 1fr "img17 img18 img18 img18" 1fr "img19 img19 img19 img20" 1fr/1fr 1fr 1fr 1fr;
}

.grid-child1 {
	grid-area: img1;
}

.grid-child1 img {
	width: 100%;
	height: 500px;
	object-fit: cover;
}

